export default {
	props: {
		show_key: Boolean
	},
	computed: {},
	data() {
		return {
			password: '', //密码
		}
	},
	methods: {
		inputNumFun(op) {
			let _this = this
			if (_this.password.length <= 6) {
				_this.password += op.num
				if (_this.password.length == 6) {
					_this.$emit('getPassword', _this.password )
				}
			}
		},
		delNumFun() {
			if (this.password.length == 0) return
			this.password = this.password.substring(0, this.password.length - 1)
		},
		forgetFun() {
			uni.showToast({
				title: '忘记密码操作',
				icon: 'none'
			})
		},
		hideFun() {
			this.$emit('hideFun')
			this.password = ''
		}
	}
}
